import { gql } from "graphql-request";

export const PRODUCT_QUERY = gql`

{
  products(first: 5) {
    edges {
      node {
        id
        title
        handle
        description
        priceRange {
          minVariantPrice {
            amount
            currencyCode
          }
        }
        images(first: 5) {  
          edges {
            node {
              src
              altText
            }
          }
        }
      }
    }
  }
}`;